body {
	margin: 0;
}

.App {
	font-family: 'jimi', Helvetica, Arial, Sans-Serif;
	width: 100%;
	height: 100%;
	position: absolute;
	overflow: hidden;
	background-color: black;
}

a {
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	color: inherit;
	text-decoration: none;
	font-size: 18px;
	font-weight: normal;
}

img {
	position: absolute;
	height: 100%;
}

.MobileApp {
	background: url("/bgmobile.jpg") no-repeat center #eee;
	background-size: cover;
	width: 100%;
	height: 100%;
	position: absolute;
  font-family: 'jimi', Helvetica, Arial, Sans-Serif;
  text-align: center;
}

.MobileMain {
  width: 100%;
  color: yellow;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.MobileMain a {
  display: block;
  margin: 50px;
  cursor: pointer;
  font-size: 24px
}

.Contact.Mobile .ContactFields {
  right: 0;
  top: 50px;
}

.Contact.Mobile .ContactField {
  font-size: 14px;
}

.Contact.Mobile .back {
	position: absolute;
  width: 100%;
	bottom: 0;
  right: 0;
  margin: 0;
	margin-bottom: 5rem;
  text-align: center;
	font-weight: bold;
}


.Main{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	text-align: center;
}

.IframeTop {
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  position: relative;
}

.IframeWrapper {
  position: relative;
  padding-bottom: 200%;
  -webkit-transform: translateY(-35.95%);
          transform: translateY(-35.95%);
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.LinkWrapper {
	position: absolute;
	bottom: 5em;
	width: 100%;
}

.LinkWrapper * {
	margin: 0 1em;
	font-size: 24px;
	font-weight: bold;
}

.Music {
  background: url("/bgmusic.jpg") no-repeat center #000;
	background-size: cover;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
}

.Music > * {
  width: 50%;
  min-width: 650px;
  margin: 0 auto;
}

.Music > .react-pdf__Document {
  margin-top: 5rem
}

.Logo {
  background: url("/musiclogo.png") no-repeat center transparent;
	background-size: contain;
  margin-top: 10rem;
  height: 50%;
  top: 0;
  left: 0;
  position: relative;
}

.MusicLinks {
  position: relative;
  top: 0;
  color: yellow;
  width: calc(150px + 30%);
  text-align: center;
}

.MusicLinks a, .MusicLinks span {
  position: relative;
  margin: 0 auto;
  cursor: pointer;
  font-size: 24px;
  color: yellow;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.MusicLinks a:first-child, .MusicLinks span:first-child {
  margin-right: 5rem;
}
.MusicLinks a:last-child, .MusicLinks span:last-child {
  margin-left: 5rem;
}

.mBack {
  position: absolute;
  top: 0;
  left: 0;
  margin: 2rem;
  font-size: 24px;
  color: yellow;
}

.mBack a {
  font-size: 18px;
}

.Contact {
	position:absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	overflow: hidden;
	background-color: #66da89;
}

.ContactJimi {
	left: -200px;
}

.ContactWrapper {
	position: relative;
	margin: 0;
	top: 0;
	height: 100%;
	width: 1200px;
	min-width: 1200px;
	margin: 0 auto;
}

.ContactFields {
	text-align: right;
	position: absolute;
	height: 300px;
	top: calc(50% - 150px);
	width: auto;
	right: 100px;
	margin: 0;
}

.ContactFields a {
	margin: 1rem;
	font-size: 48px;
}

.ContactField {
	text-transform: uppercase;
	text-align: right;
	font-weight: normal;
	margin: 1rem;
	font-size: 18px;
}

.back {
	position: absolute;
	right: 100px;
	bottom: 0;
	font-weight: bold;
	margin: 1rem;
	margin-bottom: 5rem;
}

